/* CUSTOM CSS BEGINS HERE */

p {
  font-weight: 400;
}
h6 {
  text-transform: unset;
}

.accordion-button-dark {
  box-shadow: 0px 5px 35px 0px rgba(0, 0, 0, 0.3);
  background-color: #444;
  color: #f4f4f4;
}
.ship-name-display {
  font-weight: 700;
  font-size: calc(1em + 0.2vw);
}
.rarity-ultra-rare {
  background: linear-gradient(120deg, #fbffca, #baffbf, #a7efff, #ffabff);
}
.rarity-decisive {
  background: linear-gradient(120deg, #fbffca, #baffbf, #a7efff, #ffabff);
}
.rarity-priority {
  background: #ee9;
}
.rarity-super-rare {
  background: #ee9;
}
.rarity-elite {
  background: #c4adff;
}
.rarity-rare {
  background: #9fe8ff;
}
.rarity-common {
  background: #ccc;
}
.ship-rarity-padding {
  padding: 4px;
}
.eq-rarity-padding {
  border: 4px solid grey;
}
.tooltip.show {
  opacity: 1;
}
.banner-image {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.discordBarStyle {
  width: 100%;
  color: white;
  padding: 6px;
  text-align: center;
}
.hover-link:hover {
  text-decoration: underline;
  color: #4bb5ff;
}
.fas {
  vertical-align: middle;
}
.sub-page-background {
  background-repeat: no-repeat;
  background-size: cover;
  min-height: calc(100vh - 62px);
}

.content-tables {
  width: 100%;
}

.content-tables,
.content-tables > tbody > tr,
.content-tables > tbody > tr > td {
  border: 4px double grey;
  padding: 8px;
}

.horizontalList {
  list-style-type: none;
  margin-bottom: 0px;
}

.horizontalList > li {
  display: inline-block;
  margin: 4px;
  vertical-align: top;
}

.horizontalList > li > a > h6 {
  max-width: 124px;
}

.text-center.img-raised.ship-rarity-padding {
  max-width: 124px;
  max-height: 124px;
}

.semanticDropdownDark > div > div {
  background-color: #2f3136 !important;
  color: #fff !important;
  border-color: #878787 !important;
}

.semanticDropdownDark > input {
  color: #fff;
}

.semanticDropdownDark > div > div:hover {
  background-color: #707782 !important;
}

.__react_component_tooltip {
  max-width: 300px;
}

/* CUSTOM CSS ENDS HERE */

body {
  color: $black-color;
  font-size: $font-size-base;
  font-family: $sans-serif-family;
  overflow-x: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

.main {
  position: relative;
  background: $white-color;
}
/* Animations */
.nav-pills .nav-link,
.nav-item .nav-link,
.navbar,
.nav-tabs .nav-link,
.tag,
.tag [data-role="remove"] {
  @include transition($general-transition-time, $transition-ease);
}

//transition for dropdown caret
.card a,
.bootstrap-switch-label:before {
  @include transition($fast-transition-time, $transition-ease);
}

.dropdown-toggle:after,
[data-toggle="collapse"][data-parent="#accordion"] i {
  @include transition-on-prop(
    transform,
    $fast-transition-time,
    $transition-ease
  );
}

.dropdown-toggle[aria-expanded="true"]:after,
[data-toggle="collapse"][data-parent="#accordion"][aria-expanded="true"] i {
  @include rotate-180();
}

.button-bar {
  display: block;
  position: relative;
  width: 22px;
  height: 1px;
  border-radius: 1px;
  background: $white-bg;

  & + .button-bar {
    margin-top: 7px;
  }

  &:nth-child(2) {
    width: 17px;
  }
}

.separator-line {
  height: 2px;
  width: 44px;
  background-color: $default-color;
  margin: 20px auto;

  &.separator-primary {
    background-color: $primary-color;
  }
}

.section-space {
  height: 62px;
  display: block;
}

.pull-left {
  float: left;
}
.pull-right {
  float: right;
}

.title-up {
  text-transform: uppercase;
}
